import React, { Component } from 'react';
import renderHTML from 'react-render-html';

import './App.css';

class App extends Component {
  constructor() {
    super();

    this.state = {
      stack: [],
      terminalValue: ''
    }
  }

  componentDidMount() {
    this.handleFocusInput();

    document.addEventListener('click', this.handleFocusInput.bind(this));
  }

  handleFocusInput() {
    this.nameInput.focus();
  }

  handleInputSubmit(e) {
    e.preventDefault();

    let { stack, terminalValue } = this.state;

    switch(terminalValue) {
      case 'about':
        stack.push({
          command: 'about',
          content: `
            <h2>About me</h2>

            <p>I have 6 months experience as a front-end developer, creating responsive websites using HTML5 / CSS3.</p>

            <p>Also, I've been learning JavaScript. So, I'm planning to create single-page applications for web.</p>

            <p>I have experience working freelance as well as on sight. I have also obtained an eye for detail and appreciation for design, knowledge of integrating with and developing server-side, as well as knowing how to efficiently project manage and work with clients and colleagues successfully.</p>

            <ul>
              <li> HTML5 / CSS3 technology stack</li>
              <li> 6 months of commercial web-development experience</li>
              <li> Good experience with Gulp / npm / git / SASS to build release project on HTML + CSS</li>
              <li> Strong knowledge of development processes</li>
            </ul>
          `
        });

        this.setState({ stack });
        break;
      case 'socials':
        stack.push({
          command: 'socials',
          content: `
            <h2>Socials</h2>

            <p>
              VK: <a target="blank" href="https://vk.com/levouro">https://vk.com/levouro</a>
            </p>

            <p>
              Telegram: <a target="blank" href="https://t.me/tgSapiens">https://t.me/tgSapiens</a>
            </p>
          `
        });


        this.setState({ stack });
        break;
      case 'help':
        stack.push({
          command: 'help',
          content: `
            <h2>Commands</h2>

            <p>projects - to see projects overview</p>
            <p>about - to see general information about me</p>
            <p>socials - to get my social links</p>
          `
        });

        this.setState({ stack });
        break;

      case 'projects':
        stack.push({
          command: 'projects',
          content: `
            <h2>Projects</h2>

         <section className="terminal__project">
              <h3>YiStore</h3>
              <p>Link: <a target="blank" href="https://majorcore.github.io/yistore/">https://majorcore.github.io/yistore/</a></p>

              <p>In this project my responsibility was to build a bright and minimalistic landing page for online store.</p>

              <p>
                <b>Technologies</b>:
                HTML5, CSS3 (SASS), Gulp, Flexbox
              </p>
            </section>
   <section className="terminal__project">
              <h3>Herschel Bags</h3>
              <p>Link: <a target="blank" href="https://majorcore.github.io/herschel_bags/">https://majorcore.github.io/herschel_bags/</a></p>

              <p>It was one-product oriented project. My responsibility was to build pixel perfect landing page from Zeplin.io template to HTML + CSS.</p>

              <p>
                <b>Technologies</b>:
                HTML5, CSS3 (SASS), Gulp, Flexbox
              </p>
            </section>
          `
        });

        this.setState({ stack });
        break;
      default:
        stack.push({
          command: terminalValue,
          content: 'Unknown command, please, type "help" to see list of all commands'
        });

        this.setState({ stack });
    }

    this.setState({ terminalValue: '' });
  }

  handleInputChange(e) {
    this.setState({ terminalValue: e.target.value });
  }

  render() {
    const { stack, terminalValue } = this.state;

    return (
      <main>
        <p>Please, type "help" and get list of all commands</p>

        {stack.map((terminalMessage, index) => {
          return(
            <section key={index} className="terminal-message">
              <div className="terminal-message__command">
                <div className="terminal__name">user@terminal</div>
                <div>{terminalMessage.command}</div>
              </div>

              <span>{renderHTML(terminalMessage.content)}</span>
            </section>
          );
        })}

        <section className="terminal">
          <div className="terminal__name">user@terminal</div>

          <form onSubmit={this.handleInputSubmit.bind(this)}>
            <input
              className="terminal__input"
              ref={(input) => { this.nameInput = input }}
              onChange={this.handleInputChange.bind(this)}
              value={terminalValue}
            />
          </form>
        </section>
      </main>
    );
  }
}

export default App;



